body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #1B1B32; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  background-color: #78150c;
  font-family: Lato, sans-serif;
  font-size: 18px;
}

.App {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.App div {
  padding-bottom: 1px;
}

.calc-container {
  width: 400px;
  height: 625px;
  padding: 14px;
  background-color: #364e72;
  border-radius: 20px;
}

.row {
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.row-up {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}



.button-container {
  height: 83px;
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  background-color: #1b1b32;
  border-radius: 50%;
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
  margin: 4px;
  font-weight: bold;
  color: white;
  font-size: 1.8rem;
}

.button-container:hover{
  background-color: #5F80B1;
}

.operator { 
  color: yellow;
  font-size: 2rem;
}

.operator:hover{ 
  background-color: #ff6600;
}
.input {
  height: 75px;
  border-radius: 50px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color:black;
  background-color: white;
  margin: 20px 0;
  padding: 11px 30px 11px 11px;
  font-weight: bold;
  font-size: 1.8rem;
  box-shadow: -1px 4px 1px 1px white;
}
.button-clear {
  height: 4.4rem;
  width: 4.4rem;
  /* flex: 1; */
  font-size: 1.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1b1b32;
  border-radius: 50px;
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
  margin: 8px 8px 0 0;
  font-weight: bold;
  color: white;
  border: 2px solid white;
}

.button-clear:hover{
  background-color: #ebb;
}
.logo-container {
  width: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.label-container{
  height: 83px;
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  background-color: #1b1b32;
  border-radius: 25px;
  -webkit-user-select: none;
          user-select: none;
  margin: 4px 13px 0 10px;
  font-weight: bold;
  color: white;
  font-size: 1.2rem;
  font-style: italic;
}

.text-container {
  padding: 0 10px; 
}

.App-logo {
  height: 40px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
